import React from 'react'

// MUI Components
import Grid from '@mui/material/Grid'
import Paper from '@mui/material/Paper';
import Divider from '@mui/material/Divider';
import Box from '@mui/material/Box';

// Page Components
import Calendar from '../../shared/Calendar/Calendar';
import InfoCards from '../../shared/Cards/InfoCards';
import { FaUser, FaUsers } from 'react-icons/fa';  

export default function Dashboard(props) {
    return (
        <div style={{ width: '100%', height: '100%' }}>
            <Grid container>
                <Grid item xs={9} sx={{ p: 1, pt: 1 }}>
                    <Paper square elevation={1} sx={{ p: 1 }}>
                        <Box display="flex" flexDirection="row">
                            <InfoCards icon={<FaUsers />} data={"900+"} caption="Members" />
                            <InfoCards icon={<FaUser />} data={"500+"} caption="Accounts" />
                            <InfoCards icon={<FaUsers />} data={"10000+"} caption="Transactions" />
                        </Box>
                    </Paper>
                    <Divider sx={{ mt: 2 }} /> 
                </Grid>
                <Grid item xs={3} sx={{ pr: 1, pt: 1 }}>
                    <Paper square elevation={1} sx={{ p: 1, m: 0 }}>
                        <Calendar />
                    </Paper>
                </Grid>
            </Grid>
        </div>
    )
}
